import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store.js'
import axios from "axios"
import  { ToastPlugin } from 'vux'
import _ from "lodash"


Vue.config.productionTip = false;
Vue.prototype.$axios = axios;
Vue.use(ToastPlugin);
Vue.prototype._ = _;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
