


import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import settings from "../../settings";
export default {
name: "ListManage",
components: {
XHeader,
Group,
Cell,
Scroller,
XButton,
XDialog,
XInput,
Flexbox,
FlexboxItem,
Selector,
Actionsheet
},
directives: {
TransferDom
},
data() {
return {
value: "",
showGroupForm: false,
showSongForm: false,
songList: [],
groupList: [],
selectGroupList: [],
withGroupList: [],
songAddObj: {
name: "",
group_id: null,
singer: ""
},
Loop: null,
editId: 0,
editObj: {},
showOption: false,
showGroupOption: false,
optnios: [
{
label: "编辑",
value: "edit"
},
{
label: "删除",
type: "warn",
value: "delete"
}
],
groupOptions: {
"title.noop": `确定删除当前分组吗?<br/><span style="color:#666;font-size:12px;">删除后该分组下面的歌曲会自动到默认分组</span>`,
delete: '<span style="color:red">删除</span>'
},
isEdit: true,
editGroupId: 0
};
},
created() {
this.getGroup();
this.getList();
},
methods: {
groupAdd() {
this.$axios
.post(
settings.host + "/song/group/",
{
name: this.value
},
{
headers: {
Authorization: "JWT " + localStorage.getItem("token")
}
}
)
.then(
res => {
console.log(res);
},
err => {
console.log(err);
}
);
},
addSongHandle() {
this.isEdit = false;
this.showSongForm = !this.showSongForm;
this.songAddObj = {
name: "",
group_id: null,
singer: ""
};
},
menuClickHandle(key, item) {
console.log(key, item);
if (key == "delete") {
this.$axios
.delete(settings.host + "/song/song/" + this.editId + "/")
.then(res => {
this.$vux.toast.text(res);
this.getList();
});
} else if (key == "edit") {
this.isEdit = true;
this.songAddObj.singer = this.editObj.singer;
this.songAddObj.name = this.editObj.name;
this.songAddObj.group_id = this.editObj.group_id || null;
this.showSongForm = !this.showSongForm;
}
},
itemClickHandle(item) {
console.log(item);
this.showOption = !this.showOption;
this.editId = item.id;
this.editObj = item;
},
groupClickHandle(group) {
clearTimeout(this.Loop); 
this.Loop = setTimeout(
function() {
this.editGroupId = group.id;
this.showGroupOption = !this.showGroupOption;
}.bind(this),
1500
);
},
emptyTime() {
clearTimeout(this.Loop); 
},
groupDeleteHandle(item) {
if (item == "delete") {
this.$axios
.delete(settings.host + "/song/group/" + this.editGroupId)
.then(() => {
this.getGroup();
this.getList();
this.$vux.toast.text("删除成功");
});
}
},
confirmClickHandle() {
if (this.songAddObj.name == "" || this.songAddObj.singer == "") {
this.$vux.toast.text("请填检查写内容");
return;
}
if (this.isEdit) {
this.$axios
.put(
settings.host + "/song/song/" + this.editId + "/",
this.songAddObj,
{
headers: {
Authorization: "JWT " + localStorage.getItem("token")
}
}
)
.then(res => {
console.log(res);
this.showSongForm = !this.showSongForm;
this.$vux.toast.text("编辑成功");
this.withGroupList = [];
this.getList();
});
} else {
this.$axios
.post(settings.host + "/song/song/", this.songAddObj, {
headers: {
Authorization: "JWT " + localStorage.getItem("token")
}
})
.then(res => {
console.log(res);
this.showSongForm = !this.showSongForm;
this.$vux.toast.text("添加成功");
this.withGroupList = [];
this.getList();
});
}
},
getList() {
this.$axios
.get(settings.host + "/song/song/", {
headers: {
Authorization: "JWT " + localStorage.getItem("token")
}
})
.then(
res => {
console.log(res);
this.songList = res;
for (var i = this.songList.length - 1; i > 0; i--) {
if (this.songList[i].group != null) {
var arr = this.songList.splice(i, 1);
this.withGroupList.push(arr[0]);
}
}
console.log(this.withGroupList);
},
err => {
console.log(err);
}
);
},
getGroup() {
this.$axios
.get(settings.host + "/song/group/", {
headers: {
Authorization: "JWT " + localStorage.getItem("token")
}
})
.then(
res => {
this.groupList = res;
this.selectGroupList = JSON.parse(JSON.stringify(this.groupList));
this.selectGroupList.unshift({ id: null, name: "默认" });
},
err => {
console.log(err);
}
);
}
}
};
