
import XHeader from 'vux/src/components/x-header/index.vue'

import QRCode from 'qrcodejs2'
import settings from "../../settings"
export default {
components:{
XHeader,

},
data(){
return {
qrcode:"",
codeWidth:300
}
},
methods:{
createQrcode(){
this.qrcode = new QRCode('qrcode', {
width:this.codeWidth,
height:this.codeWidth,
text:settings.domain + "/order/" + localStorage.user_id,
})
}
},
mounted(){
this.codeWidth = document.getElementById("qrcode").clientWidth-20
this.createQrcode();
}

}
