
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import settings from "../../settings"
import { mapMutations } from 'vuex';
export default {
components: {
Flexbox,
FlexboxItem
},
data(){
return {
username:"",
password:""
}
},
methods:{
...mapMutations(['changeLogin']),
login(){
if(this.username == ""||this.password == ""){
this.$vux.toast.text('请填检查写内容');
return
}
this.$axios.post(settings.host + "/users/login/", {
username:this.username,
password:this.password
}).then((res)=>{
localStorage.token = res.token;
localStorage.user_id = res.user_id;
this.changeLogin("token", res.token);
localStorage.user_name = res.user_name;
this.$vux.toast.text('登录成功');
setTimeout(()=>{
this.$router.push("/")
}, 1000)
}, (err)=>{
console.log(err);
})
}
}
};
