

import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import settings from "../../settings";
export default {
name: "ListManage",
components: {
XHeader,
Group,
Cell,
Scroller,
Confirm,
XButton,
XDialog,
XInput,
Flexbox,
FlexboxItem
},
directives: {
TransferDom
},
data() {
return {
songList: [],
user_id: 0,
orderList: [],
show: false,
song:{
sponsor:"",
money:0,
song:""
}
};
},
created() {
this.user_id = this.$route.params.id;
this.getList();
},
methods: {
getList() {
this.$axios
.get(settings.host + "/song/order/" + this.user_id, {
headers: {
Authorization: "JWT " + localStorage.getItem("token")
}
})
.then(
res => {
this.songList = res;
this.orderList = this._.groupBy(this.songList, "group.name");
},
err => {
console.log(err);
}
);
},
songClickHandle(song){
this.song.song = song.name;
this.show = !this.show;
},
songAdd() {
this.$axios
.post(
settings.host + "/song/sing/" + this.user_id + "/",
this.song,
{
headers: {
Authorization: "JWT " + localStorage.getItem("token")
}
}
)
.then(
res => {
if(res.status==201){
this.$vux.toast.text("添加成功");
this.show = !this.show;

}
},
err => {
console.log(err);
}
);
}
}
};
