import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Self from './views/Self.vue'
import Login from './views/account/Login.vue'
import ListManage from './views/Me/listManage'
import orderList from "./views/Order/orderList"
import qrcode from "./views/Me/qrcode"
import orderIndex from './views/Order/orderIndex'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            name: 'home',
            component: Home,
            meta: {
                hasTabbar: true
            }
        },
        {
            path: '/self',
            name: 'self',
            component: Self,
            meta: {
                hasTabbar: true
            }
        },
        {
            path: "/manage",
            name: "manage",
            component: ListManage,
            meta: {
                hasTabbar: false
            }
        },
        {
            path: '/login',
            name: 'login',
            component: Login,
            meta: {
                hasTabbar: false
            }
        },
        {
            path: '/order/:id',
            name: "orderList",
            component: orderList,
            meta: {
                hasTabbar: false
            }
        },
        {
            path: '/order/index/:id',
            name: 'orderIndex',
            component: orderIndex,
            meta: {
                hasTabbar: false
            }
        },
        {
            path: '/qrcode',
            name: "qrcode",
            component: qrcode,
            meta: {
                hasTabbar: false
            }
        }
    ]
})

router.beforeEach((to, from, next) => {
    if (to.path === '/login') {
        next();
    } else {
        let token = localStorage.getItem('token');

        if (token === 'null' || token === '') {
            next('/login');
        } else {
            next();
        }
    }
})

export default router;